@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
.navbar{background-color:transparent;z-index:1;flex-shrink:0;width:100%}.navbar .navbar-brand>h3{padding:0;color:#3776b0;font-size:2.1em;font-family:Gerstner;text-shadow:0px 2px 5px rgba(0,0,0,.01),0px 3px 5px rgba(0,0,0,.05),0px 3px 5px rgba(0,0,0,.1)}.navbar .navbar-brand>h3:hover{color:#969696;transition:.1s}.navbar .right-nav{text-shadow:0px 2px 5px rgba(0,0,0,.01),0px 3px 5px rgba(0,0,0,.05),0px 3px 5px rgba(0,0,0,.1)}.sticky-top{background-color:#fff;transition:all .1s linear;box-shadow:0px 1px 5px rgba(0,0,0,.01),0px 1px 5px rgba(0,0,0,.05),0px 1px 5px rgba(0,0,0,.1)}@media screen and (max-width: 602px){.navbar-brand>h3{font-size:2.1rem !important}}@font-face{font-family:Riesling;src:url(/static/media/riesling.664ca03c.ttf)}.jumbotron{position:relative;height:70vh;top:-83px}.jumbotron .home-text{position:relative;top:30%;font-size:5.9rem;line-height:.7;font-family:Riesling;color:#3776b0;text-shadow:0px 4px 5px rgba(0,0,0,.05),0px 8px 5px rgba(0,0,0,.05),0px 15px 5px rgba(0,0,0,.1)}.forum-bar{background-color:#3776b0}.thread-bar{background-color:#3776b0}@font-face{font-family:Gerstner;src:url(/static/media/gerstner.c500f3e9.ttf)}*{margin:0;padding:0;box-sizing:border-box;-webkit-box-sizing:border-box;-msbox-sizing:border-box;font-family:"Roboto",sans-serif}body{overflow-x:hidden}html{scroll-behavior:smooth}::-webkit-scrollbar{width:9px}::-webkit-scrollbar-track{background:#f1f1f1}::-webkit-scrollbar-thumb{border-radius:5px;background:#a4b1b0}.App{display:flex;min-height:87.1vh;flex-direction:column}.App .content{flex:1 1}.App .foram-area{position:relative;top:-80px}.App .footer{margin-top:5px;flex-shrink:0;height:48.5px}.App .footer .footer-bar{height:10px;background-color:#3776b0}.App .footer .footer-top{margin:0;padding:0;color:#3776b0;background:linear-gradient(to top, #3776B0, #EEF4F9);text-shadow:0px 2px 5px rgba(0,0,0,.1),0px 3px 5px rgba(0,0,0,.1),0px 3px 5px rgba(0,0,0,.1)}.App .footer .footer-bottom{text-align:center;color:#eef4f9;background-color:#3776b0}.my-threads li{list-style:none}.my-threads li:hover{color:#969696;cursor:pointer}.my-threads li:nth-of-type(2){background-color:#ff9dbd t}.found_user:hover{background-color:#ffeec8}.subject-name{color:#d0ff00}.subject-name:hover{cursor:pointer;color:#fafafa}@media screen and (max-width: 992px){.App{overflow-x:hidden}}
